@import url("https://fonts.googleapis.com/css2?family=Quantico&display=swap");
* :root {
  /* COLORS  */
  /* primary */
  --primary-0: #f0efff;
  --primary-50: #d8d6ff;
  --primary-100: #c1beff;
  --primary-200: #aaa5ff;
  --primary-300: #938dff;
  --primary-400: #7b74ff;
  /* main */
  --primary-500: #ffb338;
  --primary-600: #554ed9;
  --primary-700: #4640b3;
  --primary-800: #37338c;
  --primary-900: #282566;
  --primary-1000: #191740;
  --primary-1100: #0a0919;
  /* grey */
  --grey-0: #fafbfc;
  --grey-50: #f2f4f8;
  --grey-100: #eaedf3;
  --grey-200: #e2e7ef;
  --grey-300: #dbe0ea;
  --grey-400: #d3dae6;
  --grey-500: #cbd3e1;
  --grey-600: #adb3bf;
  --grey-700: #8e949e;
  --grey-800: #70747c;
  --grey-900: #51545a;
  --grey-1000: #333538;
  --grey-1100: #141516;

  /* rest */
  --black: #222;
  --white: #fff;
  --red-light: #f8d7da;
  --red-dark: #842029;
  --green-light: #d1e7dd;
  --green-dark: #0f5132;
  /* box shadows */
  --shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-2: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-3: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-4: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  /* typography */
  --defaultFontSize: 87.5%;
  --headingFont: "Inconsolata", monospace;
  --bodyFont: "Montserrat", sans-serif;
  --smallText: 0.8em;
  --extraSmallText: 0.7em;
  /* rest */
  --backgroundColor: var(--grey-0);
  --textColor: var(--grey-1000);
  --borderRadius: 0.25rem;
  --letterSpacing: 1px;
  --transition: 0.3s ease-in-out all;
  --max-width: 1120px;
  --fixed-width: 600px;
}

.App {
  text-align: center;
  width: 90vm;
  max-width: var(--max-width);
  margin: 0 auto;
}

.page-footer {
  text-align: center;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--black);
  color: var(--white);
}
.page-footer h5 {
  margin-bottom: 0;
}
.page-footer p {
  margin-bottom: 0;
}
.page-footer span,
.page-footer a {
  color: var(--primary-500);
}

.head {
  width: 90vm;
  max-width: var(--max-width);
}

.head .flex-container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  column-gap: 50px;
}

.head img {
  width: 200px;
  margin-left: -6px;
  /* margin-bottom: -9px; */
}
.head h3 {
  font-size: 5rem;
  /* display: inline-block; */
  /* justify-self: start; */
}

.head h2 {
  font-family: "Quantico", sans-serif;
}

.game-container {
  margin: 0 auto;
  max-width: 90vw;
  display: flex;
  justify-content: center;
}

.four {
  display: grid;
  grid-template-columns: 30vw 30vw;
  grid-gap: 1rem;
  grid-auto-flow: row;
}

.eight {
  display: grid;
  grid-template-columns: 15vw 15vw 15vw 15vw;
  grid-gap: 1rem;
  grid-auto-flow: row;
}

.twelve {
  /* grid-template-columns: auto fit-content(10%); */
  display: grid;
  grid-template-columns: 15vw 15vw 15vw 15vw;
  grid-gap: 1rem;
  grid-auto-flow: row;
}
.sixteen {
  /* grid-template-columns: auto fit-content(10%); */
  display: grid;
  grid-template-columns: 15vw 15vw 15vw 15vw;
  grid-gap: 1rem;
  grid-auto-flow: row;
}

.card-cont {
  position: relative;
  text-align: center;
  color: black;
}

.grid-img {
  width: 100%;
  height: auto;
}

.size-five {
  grid-template-columns: auto auto auto auto auto;
}

.img-text {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-transform: capitalize;
  background-color: rgba(255, 255, 255, 0.744);
  border-style: solid;
  border-color: #0f513203;
}
